<template>
  <div id="app">    
    <nprogress-container></nprogress-container>
    <router-view></router-view>
  </div>
</template>

<script>
import NprogressContainer from 'vue-nprogress/src/NprogressContainer'

export default {
  name: 'app',
  components: {
    NprogressContainer
  }
}
</script>
<style lang="scss">

// Set primary color in app
$blue: hsl(197, 100%, 47%) !default;
$primary: $blue;
$body-size: 14px !default;

@import "~bulma";
@import "~animate.css";

body, html {
  background: rgba(240, 242, 245, 1);
}
#app {
  margin :0px;
}
.nprogress-container {
  position: fixed !important;
  width: 100%;
  height: 50px;
  z-index: 2048;
  pointer-events: none;
  #nprogress {
    .bar {
      background: #4DD0E1;
      height: 2px;
    }
    .peg {
      box-shadow: 0 0 10px $primary, 0 0 5px $primary;
    }
  }
}
</style>



