<template>
  <div class="container is-fluid">
  
    <div class="columns" style="margin-top: 0;">
      <div class="column is-10 is-offset-2 content-data" style="padding-top:0;">
        <div class="column is-12 has-text-centered main-logo">
          <h1 class="title" style="color: #fff;">MITRPHOL</h1>
        </div>
        <router-view></router-view>
      </div>
    </div>
    
  </div>
</template>
<script>
import Navbar from './Navbar'
import Sidebar from './Sidebar'

export default {
  components: {
    Navbar,
    Sidebar
  }
}
</script>

<style lang="scss">
.main-logo {
  height: 66.5px;
  background:hsl(197, 100%, 47%) !important;
  padding-top:17px; 
  margin-bottom: 15px;
}
.columns {
  .navbar-top {
    background: #fff;
    -moz-box-shadow: 0px 0px 2px #dbdbdb;
    -webkit-box-shadow: 0px 0px 2px #dbdbdb;
    box-shadow: 0px 0px 2px #dbdbdb;
    position: fixed;
    width: 100vw;
    z-index: 10;
  }
  .sidebar-left {
    background: #545c6e;
    padding:0;
    position: fixed;
    z-index: 11;
    height: 100vh;
    max-height: 100vh;
    margin-top: 0 !important;
  }
  @media screen and (min-width: 768px) {
    .content-data {
      padding: 0px 25px 15px 15px;
    }
  }
}
.container.is-fluid { 
  margin:0;
}
.box {
  border-radius: 1px;
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0px 1px rgba(171, 171, 171, 0.1);
}
.column .panel {
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1);
  background: #fff; 
}
</style>



