<template>
  <div class="container is-fluid">
  
    <div class="columns" style="margin-top: 0;">
      <div class="column is-2 sidebar-left">
        <Sidebar></Sidebar>
      </div>
      <div class="column navbar-top">
        <div class="columns">
          <div class="column is-12">
            <Navbar></Navbar>
          </div>
        </div>
      </div>
      <div class="column is-10 is-offset-2 content-data">
        <router-view></router-view>
      </div>
    </div>
    
  </div>
</template>
<script>
import Navbar from './Navbar'
import Sidebar from './Sidebar'

export default {
  components: {
    Navbar,
    Sidebar
  }
}
</script>

<style lang="scss">
.columns {
  .navbar-top {
    background: #fff;
    -moz-box-shadow: 0px 0px 2px #dbdbdb;
    -webkit-box-shadow: 0px 0px 2px #dbdbdb;
    box-shadow: 0px 0px 2px #dbdbdb;
    position: fixed;
    width: 100vw;
    z-index: 10;
  }
  .sidebar-left {
    background: #545c6e;
    padding:0;
    position: fixed;
    z-index: 11;
    height: 100vh;
    max-height: 100vh;
    margin-top: 0 !important;
  }
  .content-data {
    margin-top: 70px;
    padding: 15px 15px 15px 15px;
  }
  @media screen and (min-width: 768px) {
    .content-data {
      padding: 15px 25px 15px 15px;
    }
  }
}
.container.is-fluid { 
  margin:0;
}
.box {
  border-radius: 1px;
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0px 1px rgba(171, 171, 171, 0.1);
}
.column .panel {
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1);
  background: #fff; 
}
</style>



