<template>
  <div>
    <div class="columns">
      <div class="column">
        <h1 class="title">Components</h1>
      </div>
    </div>
    <a class="button" data-target="#modal" @click="toggleModal">modal</a>
    <div class="modal" :class="{'is-active': isActive}" id="modal">
      <div class="modal-background animated fadeIn"></div>
      <div class="modal-card animated zoomIn">
        <header class="modal-card-head">
          <p class="modal-card-title">Modal title</p>
          <button class="delete"></button>
        </header>
        <section class="modal-card-body">
          <!-- Content ... -->
        </section>
        <footer class="modal-card-foot">
          <a class="button is-success">Save changes</a>
          <a class="button" @click="toggleModal">Cancel</a>
        </footer>
      </div>
      <button class="modal-close" @click="toggleModal"></button>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        isActive: false
      }
    },
    methods: {
      toggleModal () {
        this.isActive = !this.isActive
      }
    }
  }
</script>



